'use client';

import { notFound } from 'next/navigation.js';
import { ReactElement } from 'react';

import { WithAuth } from '@/app/guards/index.js';
import { PrimaryPage } from '@/app/navigation/index.js';

import { SlidesPage } from '@/pages/slides/index.js';

import { SlidesConsumer } from '@/entities/slides/index.js';

interface ArtistSlidesPageProps {
    params: {
        artistId?: string;
    };
}

const Slides = ({ params }: ArtistSlidesPageProps): ReactElement => {
    if (!params.artistId) {
        notFound();
    }

    return (
        <PrimaryPage>
            <WithAuth>
                <SlidesPage slidesConsumer={SlidesConsumer.ARTIST} artistId={params.artistId} />
            </WithAuth>
        </PrimaryPage>
    );
};

// eslint-disable-next-line import/no-default-export
export default Slides;
